.ul {
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    color: rgb(0, 0, 0);
      font-family: Montserrat;
font-size: 24px;
font-weight: 600;
line-height: 31.2px;
  }
  .ul li {
    padding: 0 .5em .25em;
    cursor: pointer;
    position: relative;
    overflow: hidden;
    transition: .3s;
  }
.ul li:hover {
    color: #ff0000;
  }
.ul li:before {
    content: "";
    position: absolute;
    inset: calc(100% - 3px) 0 0 0; /* 3px = the thickness */
    background:red; /* the color */
    scale: 0 1;
    transition: .3s, translate 0s .3s;
  }
.ul:hover li:before {
    scale: 1;
  }
.ul li:hover:before {
    translate: 0;
    transition: .3s;
  }
.ul:hover li:has(~ li:hover):before {
    translate: 100% 0;
    transition: .2s .2s,scale 0s .4s;
  }
.ul:hover li:hover ~ li:before {
    translate: -100% 0;
    transition: .2s .2s,scale 0s .4s;
  }
  
