.main {
margin: 0px 0px 80px 80px;
}
.container {
    background-color: #fff;
    padding: 0px 0px 20px 0px;
    border-radius: none;
}
h1 {
    text-align: center;
    margin-bottom: 0px;    font-family: Montserrat;
    font-size: 50px;
    font-weight: 600;
    line-height: 26px;
    margin-bottom: 40px;


}
.grid {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 10px;
   }
.grid > * {
    display: flex;
    flex-direction: column;
justify-content: center}

.p {
    font-family: Montserrat;
    font-size: 15px;
    font-weight: 600;

}
.h {
    font-family: Montserrat;
    font-size: 25px;
    font-weight: 700;
    line-height: 30px;
    text-align: left;
    margin: 0px;
    padding: 0px;

}
.card h2 {
    padding: 0px;
    font-size: 25px;
    color: #ff0000;

}


.card {
    width: 350px;
    height: 190px;
    padding: 0px 0px 0px 0px;
    border-radius: 30px;
    border: 8px 0px 0px 0px;
    opacity: 0px;
    border: 5px solid red;
    padding: 15px;
    background-color: #ffffff;
}

.cardt2 {
    width: 500px;
    height: 190px;
    padding: 00px 0px 0px 0px;
    border-radius: 36px;
    border: 8px 0px 0px 0px;
    opacity: 0px;
    border: 5px solid #000000;
    padding: 15px;
    background-color: #ffffff;
}